*{padding:0;margin:0}
body{ 
    background-color: #020631;
}

.todoinput {
    text-align: center;
    width: 55%;
    height: 3.5rem;
    background-color: white;
    border-radius: 0.25rem;
    margin: 1rem auto auto;
    display: grid;
    grid-template-columns: 84% 8% 8%;
}

.todoinput .left input {
    width: 90%;
    height: 90%;
    /* margin-top: 0.8%; */
    margin-left: 1%;
    border: unset;
    border-radius: 0.25rem;
    outline: none;
    padding-left: 0.3rem;
    font-size: large;
    font-family: inherit;
    font-weight: 500;
}

.todoinput .right,
.todoinput .rightEnd {
    display: flex;
    justify-content: center;
    align-items: center;
}

.todoinput i {
    font-size: large;
    color: #545454;
    cursor: pointer;
}

.gradient-text{
    background-image:linear-gradient(80deg,#5affa2,#ff3f3f)
}
.gradient-text,.gradient-text2{
    -webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent
}
.gradient-text2{
    background-image:linear-gradient(45deg,#87fffb,#ff9191)
}
.textCross{
    text-decoration:line-through
}
i{
    cursor:pointer
}
.toDoInput i{
    font-size:large;color:#545454
}
.container i{
    font-size:larger;margin-top:.16rem
}
.container .toDo .bin i{
    color:#454545
}
.container .toDo .close i{
    color:brown
}
.container .toDo .tick i{
    color:#2e8b57
}
.container .toDo .recycle i{
    color:#2e4ba4
}
.mainHeading{
    margin-top:2rem
}
.mainHeading,.subHeading{
    text-align:center;color:#fff
}
.toDoInput{
    width:30%;height:3.5rem;background-color:#fff;box-shadow:0 0 2px 1px #fff;margin:1rem auto auto;
    border-radius:.25rem;display:grid;grid-template-columns:84% 8% 8%
}
.toDoInput .left input{
    width:98%;height:90%;margin-top:.8%;margin-left:1%;border:unset;border-radius:.25rem;outline:none;padding-left:.3rem;font-size:x-large;
    font-family:inherit;font-weight:700
}
.container,.toDoInput .right,.toDoInput .rightEnd{
    display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center
}
.container{
    width:30%;float:left;margin-top:2.5%;margin-left:2.5%;padding-top:1rem;padding-bottom:1rem;-webkit-flex-direction:column;
    flex-direction:column
}
.container,.container .toDo{
    height:100%;border-radius:.25rem
}
.container .toDo{
    width:92%;background-color:hsla(0,0%,94.5%,.2);margin-top:.5rem;padding:5px;transition:width .2s
}
.container .toDo:hover{
    background-color:hsla(0,0%,94.5%,.313);width:95%
}
.container .toDo .top p{
    word-wrap:break-word;text-align:justify;font-size:large;font-weight:700;cursor:default
}
.container .toDo .bottom p{
    font-size:small;cursor:default
}
.container.done{
    background-color:#2e8b57;box-shadow:0 0 2px 2px #2e8b57
}
.container.done .toDo{
    display:grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto
}
.container.done .toDo .top{
    width:25.5vw
}
.container.done .toDo .left{
    grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:3
}
.container.done .toDo .right{
    grid-column-start:3;grid-column-end:4;grid-row-start:1;grid-row-end:3
}
.container.onGoing{
    background-color:#b8860b;box-shadow:0 0 2px 2px #b8860b
}
.container.onGoing .toDo{
    display:grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto
}
.container.onGoing .toDo .top{
    width:24vw
}
.container.onGoing .toDo .left{
    grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:3
}
.container.onGoing .toDo .right{
    grid-column-start:3;grid-column-end:4;grid-row-start:1;grid-row-end:3
}
.container.dropped{
    background-color:brown;box-shadow:0 0 2px 2px brown
}
.container.dropped .toDo{
    display:grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto
}
.container.dropped .toDo .top{
    width:23.5vw
}
.container.dropped .toDo .left{
    grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:3
}
.container.dropped .toDo .right{
    grid-column-start:3;grid-column-end:4;grid-row-start:1;grid-row-end:3
}

@media only screen and (max-width:950px) and (min-width:430px){
    .container.done .toDo .top{
        width:23vw
    }
    .container.onGoing .toDo .bottom,.container.onGoing .toDo .top{
        width:18.5vw;margin-left:1vw
    }
    .container.dropped .toDo .top{
        width:19vw;margin-left:1vw
    }
    .container.dropped .toDo .bottom{
        margin-left:1vw
    }}
    @media only screen and (max-width:800px){
        .toDoInput{width:95%}}@media only screen and (max-width:430px){
            .container{width:94.82%}.toDoInput .left input{width:92%}.container.done .toDo 
            .top{width:80vw}.container.dropped .toDo .top,.container.onGoing .toDo .top{width:70vw}}
